<template>
  <div class="showmsg">
    <div class="title">
      <i class="icon iconfont icon-duigou"></i>
      <span v-show="!isshowMsg">报名成功！</span>

      <span v-show="isshowMsg">恭喜，您的作品已晋级决赛，请前往 <span style="color: #f76c1a" @click="playout">我的复赛</span> 填报复赛视频课件</span>
      <table>
        <tr>
          <td>姓名</td>
          <td>{{this.msg.preliminary1.name}}</td>
        </tr>

        <tr>
          <td>性别</td>
          <td>{{val.preliminary1.sex}}</td>
        </tr>

        <tr>
          <td>出生年月</td>
          <td>{{val.preliminary1.region}}　({{age}}岁)</td>
        </tr>

        <tr>
          <td>联系电话</td>
          <td>{{msg.preliminary1.call}}</td>
        </tr>

        <tr>
          <td>职称</td>
          <td>{{msg.preliminary1.professional}}</td>
        </tr>

        <tr>
          <td>授课年级</td>
          <td>{{msg.preliminary1.grade}}</td>
        </tr>

        <tr>
          <td>任职学校</td>
          <td>{{msg.preliminary1.school}}</td>
        </tr>

        <tr>
          <td>联系地址</td>
          <td>{{msg.preliminary1.address}}</td>
        </tr>

        <tr>
          <td>参赛性质</td>
          <td>
            <span style="color: #000;font-size: 12px">{{msg.preliminary1.nature}}</span>
            <span style="color: #000;font-size: 12px" v-if="msg.preliminary1.nature1">({{msg.preliminary1.nature1}})</span>
          </td>
        </tr>


        <tr>
          <td>课件名称</td>
          <!--<td>{{this.msg.preliminary2.coursewareName}}</td>-->
          <td :title="'[初赛课件]'+val.preliminary1.school+'-'+val.preliminary1.name+'-'+this.val.preliminary1.call.slice(-4)">[初赛课件]{{ val.preliminary1.school }}-{{val.preliminary1.name}}-{{this.val.preliminary1.call.slice(-4)}}</td>
        </tr>

        <tr>
          <td>教学设计</td>
          <td :title="'[教学设计]'+ val.preliminary1.school +'-'+ val.preliminary1.name+'-'+this.val.preliminary1.call.slice(-4)+'.'+this.msg.preliminary2.fileOne.split('.')[1]">[教学设计]{{ val.preliminary1.school }}-{{val.preliminary1.name}}-{{this.val.preliminary1.call.slice(-4)}}.{{this.msg.preliminary2.fileOne.split('.')[1]}}</td>
        </tr>

        <tr>
          <td>演示课件</td>
          <!--<td>{{this.msg.preliminary2.fileTwo}}</td>-->
          <td :title="'[演示课件]'+ val.preliminary1.school +'-'+val.preliminary1.name+'-'+this.val.preliminary1.call.slice(-4)+'.'+this.msg.preliminary2.fileTwo.split('.')[1]">[演示课件]{{ val.preliminary1.school }}-{{val.preliminary1.name}}-{{this.val.preliminary1.call.slice(-4)}}.{{this.msg.preliminary2.fileTwo.split('.')[1]}}</td>
        </tr>

        <tr v-if="this.msg.preliminary2.fileThree">
          <td>评测练习</td>
          <!--<td>{{this.msg.preliminary2.fileThree}}</td>-->
          <td :title="'[评测练习]'+ val.preliminary1.school +'-'+ val.preliminary1.name+'-'+this.val.preliminary1.call.slice(-4)+'.'+this.msg.preliminary2.fileThree.split('.')[1]">[评测练习]{{ val.preliminary1.school }}-{{val.preliminary1.name}}-{{this.val.preliminary1.call.slice(-4)}}.{{this.msg.preliminary2.fileThree.split('.')[1]}}</td>
        </tr>

      </table>
    </div>
  </div>
</template>

<script>
  import store from '../vuex/store'

  export default {
    name: "preliminary3",
    data() {
      return {
        msg:'',
        call:'',
        age:'',
        isshowMsg:true
      }
    },
    props:{
      val:{
        type:Object
      }
    },
    created(){
      console.log(this.val.preliminary1);
      var age = this.val.preliminary1.region.slice(0,4)
      var myDate = new Date();
      this.age = myDate.getFullYear() - (age-0)


      this.msg = this.$store.state.preliminary

      if(sessionStorage.getItem('isshow') =='false'){
        this.isshowMsg = false
      }else if(sessionStorage.getItem('isshow') =='true'){
        this.isshowMsg = true
      }
    },

    // updated(){
    //   this.msg = this.$store.state.preliminary
    //   console.log(this.msg);
    // },
    methods:{
      playout(){
        this.$router.push({name:'playoff'})
      }
    },
    store
  }
</script>

<style scoped lang="scss">
  .showmsg {
    .title {
      margin-top: 40px;
      span,i{
        color: #55a532;
        font-size: 16px;
      }
      p{
        font-size: 16px;
      }
      table{
        /*border: 1px solid #000;*/
        margin-top: 20px;
        tr{
          /*border: 1px solid #000;*/
          width: 400px;
          line-height: 2;
          text-align: left;
        }
        td{
          width: 200px;
          display: inline-block;
          text-align: left;
          /*border: 1px solid #000;*/
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
    }
  }

</style>



